* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-size: cover;
  width: 100%;
  height: 100vh;
  background:url("https://wallpaperplay.com/walls/full/9/5/8/206175.jpg");
  backdrop-filter: blur(20px);
}
a {
  text-decoration: none;
}
.App {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

header {
    width: 100%;
    height: 60px;
    box-shadow: 0px 0px 4px #1d1d1d;
    display: flex;
    justify-content: space-between;
    align-items:center;
}

.nav-list {
    display: flex;
    list-style: none;
}

.nav-item {
    margin-right: 20px;
    font-family: sans-serif;
    text-transform: uppercase;
    text-decoration: none;
    color: #1d1d1d;
}

.cards-container {
  width: 80%;
  max-height: 80vh;
  margin: 50px auto 0;
  overflow-y: auto;
  background: hsla(0, 0%, 100%, 0.1);
  border-radius: 30px;
}
.card-container {
  width: 80%;
  padding: 30px;
  text-align: center;
  margin: 30px auto;
  position: relative;
  box-shadow: 0 0 5px #444;
  border-radius: 10px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  display: flex;
  align-items: center;
}
.card-container:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
  box-shadow: 0 0 15px #555;
}

.card-title {
  font-family: sans-serif;
  font-weight: 100;
}
.card-title,
small {
  color: #fff;
  width: 100%;
}
small {
  position: absolute;
  bottom: 5px;
  left: 0;
  text-decoration: underline;
  font-family: sans-serif;
}

.form-container {
  width: 100%;
  margin-top: 30px;
}
.form-container form {
  width: 100%;
  display:flex;
  justify-content: center;
}
.search-box {
  background: rgba(255,255,255,0.3);
  color: #fff;
  border: none;
  border-radius: 35px;;
  padding: 15px 50px;
  outline: none;
  font-size: 18px;
  width: 80%;
  transition: .6s;
}
.search-box:focus {
    box-shadow: 0px 0px 15px #fff;
}
.submit {
  display: none;
}

svg {
    width: 18px;
    position: absolute;
    top: 105px;
    right: 60px;
}



.splash-container {
    position: absolute;
    top:0px;
    left: 0px;
    width:100%;
    height: 100vh;
	display:flex;
	justify-content:center;
	align-items:center;
	flex-wrap:wrap;
    background: #000;
    animation: vanish .3s 5s ease-in-out forwards;
}

.splash-container img {
  width: 100%;
}

.logo-container {
    width:100%;
	height:100%;
	display:flex;
	justify-content:space-between;
	align-items:center;
    flex-wrap: wrap;
    margin-left: 10px;
}

@keyframes vanish {
    100% {
        opacity: 0;
    }
}
